
.info{
  width: 60%; margin: 100px auto;
}

dl{
  counter-reset: list-number;  /* 카운터를 쓰겠다 정의 */
}
dt{
  font-weight: bold;
  background-color: #eee;
  padding: 10px 0;
  text-align: center;
}
dd{
  padding: 10px 0; margin: 0; 
  border-bottom: 1px solid #ddd;
  counter-increment: list-number;
}
dd::before{
  content:counter(list-number) '. ';
}

*{line-height: 2;}

header{
  text-align: center;
  padding-top: 50px;
}
/* css에서 Link 태그 잡을 때 a로 잡으면 됨 */
header a{margin: 0 20px;}